export default {
  data() {
    return {
      isDarkMode: false
    };
  },
  methods: {
    toggleDarkMode() {
      this.isDarkMode = !this.isDarkMode;
      this.$colorMode.preference =
        this.$colorMode.value == 'light' ? 'dark' : 'light';
    }
  }
};
